import './projects.css';
import { useState, useEffect } from 'react';

const Projects = () => {
    const [active, setActive] = useState(0);
    const [activePrev, setActivePrev] = useState(11);
    const [activePrev2, setActivePrev2] = useState(10);
    const [activeSec, setActiveSec] = useState(1);
    const [activeSec2, setActiveSec2] = useState(2);
    const [activeSec3, setActiveSec3] = useState(3);
    const [activeSec4, setActiveSec4] = useState(4);
    const [MassImagesBlockOverflow, setMassImagesBlockOverflow] = useState()

    const pageFunctionto = (value) => {
        switch (value) {
            case (0):
                document.querySelector('.btnBefore').style.pointerEvents = 'none';
                setActivePrev(prev => prev <= 0 ? 11 : prev - 1);
                setActivePrev2(prev => prev <= 0 ? 11 : prev - 1);
                setActiveSec(prev => prev <= 0 ? 11 : prev - 1);
                setActiveSec2(prev => prev <= 0 ? 11 : prev - 1);
                setActiveSec3(prev => prev <= 0 ? 11 : prev - 1);
                setActiveSec4(prev => prev <= 0 ? 11 : prev - 1);
                setActive(prev => prev <= 0 ? 11 : prev - 1);
                setTimeout(() => {document.querySelector('.btnBefore').style.pointerEvents = 'all'}, 600)
                break;
            case (1):
                document.querySelector('.btnAfter').style.pointerEvents = 'none';
                setActive(prev => prev >= 11 ? 0 : prev + 1);
                setActivePrev(prev => prev >= 11 ? 0 : prev + 1);
                setActivePrev2(prev => prev >= 11 ? 0 : prev + 1);
                setActiveSec(prev => prev >= 11 ? 0 : prev + 1);
                setActiveSec2(prev => prev >= 11 ? 0 : prev + 1);
                setActiveSec3(prev => prev >= 11 ? 0 : prev + 1);
                setActiveSec4(prev => prev >= 11 ? 0 : prev + 1);
                setTimeout(() => {document.querySelector('.btnAfter').style.pointerEvents = 'all'}, 600)
                break;
        }
    }

    useEffect(() => {
        setMassImagesBlockOverflow(document.querySelectorAll('.ImagesBlockOverflow'));
    }, [])

    useEffect(() => {
        console.log(activePrev);
        console.log(active);
        console.log(activeSec);
        if (MassImagesBlockOverflow !== undefined) {
            MassImagesBlockOverflow[activePrev].classList.remove('active');
            MassImagesBlockOverflow[active].classList.add('active');
            MassImagesBlockOverflow[activeSec].classList.remove('active');

            MassImagesBlockOverflow[activePrev2].style.opacity = '0';
            MassImagesBlockOverflow[activePrev].style.opacity = '1';
            MassImagesBlockOverflow[activePrev2].style.transform = 'translateX(-135%)';
            MassImagesBlockOverflow[activePrev].style.transform = 'translateX(-135%)';
            MassImagesBlockOverflow[active].style.opacity = '1';
            MassImagesBlockOverflow[active].style.transform = 'translateX(0%)';
            MassImagesBlockOverflow[activeSec].style.opacity = '1';
            MassImagesBlockOverflow[activeSec2].style.opacity = '1';
            MassImagesBlockOverflow[activeSec].style.transform = 'translateX(135%)';
            MassImagesBlockOverflow[activeSec2].style.transform = 'translateX(248%)';
            MassImagesBlockOverflow[activeSec3].style.opacity = '1';
            MassImagesBlockOverflow[activeSec3].style.transform = 'translateX(370%)';
            MassImagesBlockOverflow[activeSec4].style.opacity = '0';
            MassImagesBlockOverflow[activeSec4].style.transform = 'translateX(370%)';

            console.log(MassImagesBlockOverflow);
        }
    }, [active])

    return (
        <div className="Projects Projects_section sec2">
            <div className='Projects_block element-animation'>
                <div className='hiddenOverflow'>
                    <h2 className=''></h2>
                    <div className='textBlock textBlock_first active' id="first" style={{ transform: 'translateY(0px)', transition: 'all 1s cubic-bezier(0,.11,.29,1)' }}>
                        <div className='textBlock_text'>
                               ()
                        </div>
                        <div className='textBlock_text_content'>
                            : , <br />
                            :     I
                        </div>
                        <div className='Button_Learn_More'>
                            <div className='title'> </div>
                            <div>
                                <svg width="29" height="19" viewBox="0 0 29 19" xmlns="http://www.w3.org/2000/svg">
                                    <path fillRule="evenodd" clipRule="evenodd" d="M20.1562 0C20.1562 4.83249 24.0738 8.75 28.9062 8.75V9.75C24.0738 9.75 20.1562 13.6675 20.1562 18.5H19.1562C19.1562 14.661 21.375 11.3402 24.6001 9.75H0V8.75H24.6001C21.375 7.15981 19.1562 3.83901 19.1562 0H20.1562Z" />
                                </svg>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='hiddenOverflow2'>
                    <div className='ImagesBlockOverflow active' style={{ transform: 'translateX(0%)', opacity: '1' }}></div>
                    <div className='ImagesBlockOverflow' style={{ transform: 'translateX(135%)', opacity: '1' }}></div>
                    <div className='ImagesBlockOverflow' style={{ transform: 'translateX(248%)', opacity: '1' }}></div>
                    <div className='ImagesBlockOverflow' style={{ transform: 'translateX(370%)', opacity: '1' }}></div>
                    <div className='ImagesBlockOverflow' style={{ transform: 'translateX(370%)', opacity: '1' }}></div>
                    <div className='ImagesBlockOverflow' style={{ transform: 'translateX(370%)', opacity: '1' }}></div>
                    <div className='ImagesBlockOverflow' style={{ transform: 'translateX(370%)', opacity: '1' }}></div>
                    <div className='ImagesBlockOverflow' style={{ transform: 'translateX(370%)', opacity: '1' }}></div>
                    <div className='ImagesBlockOverflow' style={{ transform: 'translateX(370%)', opacity: '1' }}></div>
                    <div className='ImagesBlockOverflow' style={{ transform: 'translateX(370%)', opacity: '1' }}></div>
                    <div className='ImagesBlockOverflow' style={{ transform: 'translateX(-135%)', opacity: '1' }}></div>
                    <div className='ImagesBlockOverflow' style={{ transform: 'translateX(-135%)', opacity: '1' }}></div>
                    <div className='buttonsBlock'>
                        <div className='btnBefore btnHoverProjects' style={{pointerEvents: 'all'}} onClick={() => pageFunctionto(0)}>
                            <svg width="29" height="19" viewBox="0 0 29 19" fill="none" xmlns="http://www.w3.org/2000/svg" style={{ transform: 'scale(-1,1)' }}>
                                <path fillRule="evenodd" clipRule="evenodd" d="M20.1562 0C20.1562 4.83249 24.0738 8.75 28.9062 8.75V9.75C24.0738 9.75 20.1562 13.6675 20.1562 18.5H19.1562C19.1562 14.661 21.375 11.3402 24.6001 9.75H0V8.75H24.6001C21.375 7.15981 19.1562 3.83901 19.1562 0H20.1562Z" fill="white" />
                            </svg>
                        </div>
                        <div className='btnAfter btnHoverProjects' style={{pointerEvents: 'all'}} onClick={() => pageFunctionto(1)}>
                            <svg width="29" height="19" viewBox="0 0 29 19" fill="none" xmlns="http://www.w3.org/2000/svg" style={{ transform: 'scale(1,1)' }}>
                                <path fillRule="evenodd" clipRule="evenodd" d="M20.1562 0C20.1562 4.83249 24.0738 8.75 28.9062 8.75V9.75C24.0738 9.75 20.1562 13.6675 20.1562 18.5H19.1562C19.1562 14.661 21.375 11.3402 24.6001 9.75H0V8.75H24.6001C21.375 7.15981 19.1562 3.83901 19.1562 0H20.1562Z" fill="white" />
                            </svg>
                        </div>
                    </div>
                    <div className='StatusBar'>
                        <div className='status'>{active <= 8 && active >= 0 ? `0${active + 1}` : active + 1}</div>
                        <div className='lineStatus'></div>
                        <div className='allCount'>12</div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Projects;
